.list {
  padding: 0;
  list-style: none;
  font-weight: 600;
}

.listItem {
  text-decoration: none;
  width: 35vw;
  background-color: rgb(56, 56, 216);
  margin: 5px;
  border-radius: 3px;
}

.listLink {
  text-decoration: none;
  color: rgb(219, 195, 195);
  width: 100%;
  height: 100%;
  display: block;
  padding: 10px;
}

.listItem:hover {
  background-color: rgb(18, 18, 182);
}
