form {
  width: 35vw;
  display: flex;
}

.button {
  background-color: rgb(56, 56, 216);
  color: rgb(219, 195, 195);
  padding: 5px 10px;
  margin: 5px;
  border: none;
  border-radius: 3px;
  font-weight: 500;
  cursor: pointer;
}

.button:hover {
  background-color: rgb(18, 18, 182);
}

.input {
  padding: 5px 10px;
  margin: 5px;
  border-radius: 3px;
  max-width: 100%;
  flex-grow: 3;
}
